import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Menu } from '../../../modelo/Menu'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'menu-dados-details',   templateUrl: 'menu_dadosDetails.component.html' }) export class MenuDadosDetailsComponent implements OnInit {    menu: Menu;    private sub: Subscription;    private errorMessage: string;    constructor(private route: ActivatedRoute, private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.menu.Menu";                 let id = +params['id'];         this.menu = new Menu();         this.menu.cd_menu = id;         o.objetoJson = this.menu;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a menu. '; }      this.menu = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   } }


